.carousel-item {
  width: 100%;
  position: relative;
}
.carousel-item:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0;
  background: rgba(46, 46, 46, 0.8);
}

.slick-prev,
.slick-next {
  z-index: 1;
  width: 50px;
  height: 50px;
}
.slick-prev {
  left: 10px;
}
.slick-next {
  right: 10px;
}

.carousel-wrapper .list-thumbnails {
  display: flex;
  width: 100%;
  margin: 10px 5px;
  overflow: scroll;
}
.carousel-wrapper .list-thumbnails .thumbnail_item {
  width: 90px;
  height: 60px;
  cursor: pointer;
}
.carousel-wrapper .list-thumbnails .thumbnail_item:not(:first-child) {
  margin-left: 8px;
}
