/* Mobile Portrait View */

@media screen and (max-width: 479px) {
  * {
    box-sizing: border-box;
  }
  .toggle a:before {
    font-size: 25px;
    left: 25px;
    transform: translateY(10px);
  }
  #slider {
    margin-top: 20px;
  }
  #slider .top-bar {
    text-align: center;
  }
  .nivo-controlNav {
    display: none !important;
  }
  .nivo-caption {
    width: 95% !important;
    bottom: 32px !important;
    padding: 10px;
    left: 0 !important;
    background-color: transparent !important;
  }
  .nivo-caption p {
    display: none !important;
  }
  .nivo-caption .button {
    font-size: 12px;
    padding: 3px 5px;
  }
  .nivo-caption h2 {
    font-size: 13px !important;
    line-height: 32px !important;
    text-align: center;
    margin-bottom: 15px;
    padding: 0 !important;
  }
  .nivo-caption .slide-button {
    padding: 5px 15px !important;
  }
  .container {
    width: 95%;
  }
  /* Header Css */
  #topbar {
    padding: 10px 0;
  }
  .top-inner {
    width: 95%;
  }
  .top-right,
  .top-left {
    float: none;
    text-align: center;
    margin-left: 0;
  }
  .top-right p {
    float: none;
    text-align: center;
  }
  #topbar .top-left p {
    float: none;
    line-height: 30px;
  }

  #header {
    width: 100%;
    padding: 0;
    position: inherit !important;
    margin-top: 0;
  }
  .header-inner {
    width: 95%;
    padding-bottom: 0;
    border-bottom: none;
  }
  #header .logo {
    text-align: center;
    position: relative;
    top: 0;
  }
  .toggle {
    display: block;
  }
  .toggle a {
    width: auto;
    padding: 28px 25px;
    text-align: left;
    border-left: 1px solid #ccc;
  }
  .toggle a.active {
    color: #ffffff;
  }
  .main-nav {
    display: none;
  }
  .main-nav {
    float: none;
    margin: 0;
    position: relative;
    overflow-x: hidden;
  }
  .main-nav ul {
    background: #000000;
    color: #000;
    width: 100%;
    float: none !important;
    margin-left: 0;
  }
  .main-nav ul li {
    border-top: 1px #303030 solid;
    display: block;
    float: none;
    text-align: left;
  }
  .main-nav ul li a {
    padding: 5px 10px;
    display: block;
    color: #ffffff !important;
  }
  .main-nav ul li ul,
  .nav ul li ul ul {
    display: block !important;
  }
  #header .header-inner .main-nav ul li:hover ul {
    background: none;
  }
  .main-nav ul li ul li a:before {
    content: "\00BB \00a0";
  }
  .main-nav ul li ul li a {
    padding-left: 20px !important;
  }
  .main-nav ul li ul li ul li a {
    padding-left: 30px !important;
  }
  /* ------ Extra ------ */

  #sitemain iframe {
    max-width: 100%;
    height: auto;
  }
  /* --------- Pages ------- */

  .content-area {
    width: 95%;
    margin: 0 auto;
    padding: 0;
  }
  .site-main {
    width: 100%;
    margin: 0;
    padding: 0;
    float: none;
  }
  #sidebar {
    width: auto;
    margin: 60px 0 0;
    padding: 0;
    float: none;
  }
  .nivo-directionNav a {
    top: 36% !important;
  }
  section {
    padding: 0;
  }
  .pagearea-inner {
    position: inherit;
    top: 0;
    margin-top: 20px;
  }
  .services-box {
    width: 100%;
    margin: 0 0 20px;
    border-right: none;
  }
}
