* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

body {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.89;
  font-family: "Karla";
  letter-spacing: 0em;
  color: #888;
  margin: 0;
  padding: 0;
  background-color: #fff;
  overflow-x: hidden;
  box-sizing: border-box;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

img {
  margin: 0;
  padding: 0;
  border: none;
}

.bypostauthor {
  margin: 0;
  padding: 0;
}

section img,
img {
  width: 100%;
  height: auto;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 20px;
  padding: 0;
  line-height: 1;
  font-weight: 700;
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 25px;
}

h3 {
  font-size: 22px;
}

h4 {
  font-size: 19px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

.left {
  float: left;
}

.right {
  float: right;
}

strong,
b {
  font-weight: 700;
}

blockquote {
  position: relative;
  margin-bottom: 15px;
  font-size: 19px;
  background-color: #f9f9f9;
}

blockquote.align-left {
  padding: 20px 20px 20px 40px;
  text-align: left;
  font-weight: 400;
}

blockquote.align-left:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 5px;
  bottom: 0;
  background-color: #dda668 !important;
}

blockquote.align-right {
  padding: 10px 40px 20px 20px;
  text-align: right;
  font-weight: 400;
}

blockquote.align-right:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 5px;
  bottom: 0;
  background-color: #dda668 !important;
}

hr,
.hr {
  border-bottom: 1px solid #cfd0d2 !important;
  clear: both;
  margin: 30px 0;
  border: none;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  transition-delay: 1s;
  transition: 0.6s;
}

a:hover {
  text-decoration: none;
}

a:focus {
  outline: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

iframe {
  width: 100%;
  display: block;
  margin: 0;
}

article ul,
article ol {
  margin: 0 0 15px 15px;
}

article ul li {
  list-style: disc;
}

.custom-height {
  min-height: 20px;
}

.clear {
  clear: both;
}

.middle-align {
  margin: 0 auto;
  padding: 0;
}

.wp-caption {
  margin: 0;
  padding: 0;
  font-size: 13px;
  max-width: 100%;
}

.wp-caption-text {
  margin: 0;
  padding: 0;
}

.sticky {
