}

.header {
  padding: 0;
  position: relative;
  z-index: 10;
  left: 0;
  right: 0;
  top: 0;
  background-color: #242c3a;
  padding: 0;
}

.header-inner {
  width: 1170px;
  margin: 0 auto;
  position: relative;
}

.header-right {
  float: right;
}

.head-button {
  float: right;
  margin-top: 24px;
  margin-left: 50px;
  text-align: center;
}

.head-button a {
  display: block;
  background-color: #ffb300;
  text-transform: uppercase;
  padding: 10px 30px;
  font-weight: 700;
  color: #242c3a;
