}

.head-button a:hover {
  background-color: #ffb300;
  color: #fff;
}

.logo {
  float: left;
  text-align: left;
  transition: 0.3s;
  -moz-transition: 0.3s;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  padding: 15px 0;
}

.logo img {
  width: auto;
  margin-top: 15px;
}

.logo h1 {
  font-weight: 400;
  margin: 0 0 4px;
  text-transform: capitalize;
  line-height: 44px;
  font-size: 40px;
  font-family: "Oswald";
  color: #dda668;
  letter-spacing: 4px;
}

.logo p {
  font-size: 13px;
  color: #fff;
  display: block;
  line-height: normal;
  letter-spacing: 2px;
}

.toggle {
  display: none;
  position: relative;
  right: 0;
  top: 0;
  margin: 18px 0 0;
  text-transform: uppercase;
  font-weight: 700;
}

.toggle a {
  width: auto;
  padding: 28px 25px;
  text-align: left;
  border-left: 1px solid #ccc;
  color: #787878;
}

.toggle a:before {
  content: "\f0c9";
  color: #fff;
  font-family: FontAwesome;
  position: absolute;
  top: 5px;
  font-size: 28px;
  left: 20px;
}

.sitenav {
  position: relative;
  z-index: 9999;
  text-align: center;
  float: right;
  margin-top: 18px;
}

.sitenav ul {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed";
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.sitenav ul li {
  display: inline-block;
  position: relative;
  line-height: normal;
}

.sitenav ul li a {
  padding: 20px 12px 35px;
  display: block;
  text-align: center;
  color: #fff;
  position: relative;
}

.sitenav ul li.current_page_item a,
.sitenav ul li a:hover {
  color: #dda668;
}

.sitenav ul li a.parent:before {
  content: "\f107";
  position: absolute;
  right: 0;
  top: 19px;
  font-family: FontAwesome;
  font-size: 18px;
}

.sitenav ul li a.parent {
  padding-right: 25px;
}

.sitenav ul li ul {
  display: none;
  z-index: 9999;
  position: absolute;
  left: -9999px;
}

.sitenav ul li:hover > ul {
  display: block;
  width: 200px;
  position: absolute;
  left: auto;
  top: 72px;
  text-align: left;
  -moz-animation: fadeInUp 0.3s ease-in-out;
  -webkit-animation: fadeInUp 0.3s ease-in-out;
  animation: fadeInUp 0.3s ease-in-out;
}

.sitenav ul li ul li {
  display: block;
  position: relative;
  float: none;
  top: 1px;
  line-height: 18px;
  margin-left: 0;
}

.sitenav ul li ul li a {
  display: block;
  padding: 10px;
  position: relative;
  top: 0;
  text-align: left;
}

.sitenav ul li ul li a.parent:before {
  content: "\f105";
  position: absolute;
  right: 8px;
  top: 9px;
  font-family: FontAwesome;
  font-size: 18px;
}

.sitenav ul li:hover ul li:hover > ul {
  display: block;
  position: absolute;
  left: 200px;
  top: -1px;
  -moz-animation: fadeInRight 0.3s ease-in-out;
  -webkit-animation: fadeInRight 0.3s ease-in-out;
  animation: fadeInRight 0.3s ease-in-out;
}

.social-icons a {
  width: 38px;
  height: 54px;
  line-height: 54px;
  display: inline-block;
  padding: 0;
  transition: 0.6s;
  -moz-transition: 0.6s;
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  font-size: 16px;
  color: #dda668;
  position: relative;
  margin: 0 1px;
  text-align: center;
}

.social-icons a:hover {
  color: whitesmoke;
}

.social-icons br {
  display: none;
}

.boxlayout .nivo-caption {
  bottom: 30%;
  width: 60%;
}

#slider {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

#slider:before {
  background-color: rgba(0, 0, 0, 0.5);
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  z-index: 6;
  width: 100%;
  height: 100%;
}

.slider-main {
  min-height: 600px;
  position: relative;
  background: url(../images/loading.gif) no-repeat center center #fff;
  overflow: hidden;
}

.boxlayout .slider-main {
  min-height: 650px;
}

#slider a.button {
  margin-right: 20px;
}

a.main-button {
  background-color: #dda668;
  color: #fff;
  padding: 16px 40px;
  font: 500 16px/20px "Roboto";
  display: inline-block;
  transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  -moz-transition: all 0.5s ease 0s;
  -webkit-transition: all 0.5s ease 0s;
  text-transform: capitalize;
  position: relative;
}

a.main-button:hover {
  background-color: #343c49;
  color: #fff;
}

a.button2 {
  color: #151515;
  padding: 15px 50px;
  font: 700 14px/20px "Roboto";
  display: inline-block;
  transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  -moz-transition: all 0.5s ease 0s;
  -webkit-transition: all 0.5s ease 0s;
  text-transform: uppercase;
  border: 2px solid;
  position: relative;
  overflow: hidden;
}

a.button2:after {
  content: "";
  position: absolute;
  z-index: -1;
  -webkit-transition: all 1.2s;
  -moz-transition: all 1.2s;
  transition: all 1.2s;
  width: 100%;
  height: 0;
  top: 50%;
  left: 50%;
  background: #e12b15;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -moz-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

a.button2:hover::after {
  height: 500%;
  opacity: 1;
}

a.button2:hover {
  color: #fff;
  border-color: #e12b15;
}

a.buttonstyle1 {
  text-transform: uppercase;
  color: #fff;
  background-color: #000;
  border-radius: 30px;
  padding: 14px 40px;
  font: 700 15px/22px "Roboto";
  display: table;
}

a.buttonstyle1:hover {
  background-color: #e12b15;
  color: #fff;
}

.custombtn {
  margin-top: 30px;
}

.section_head {
  text-align: center;
  margin-bottom: 60px;
}

h2.section_title {
  font: 400 50px/58px "Roboto Slab";
  color: #1a2c56;
  position: relative;
  text-transform: capitalize;
  margin-bottom: 30px;
  padding-bottom: 30px;
}

h2.section_title:before {
  content: "\f005  \f005  \f005";
  font-family: FontAwesome;
  position: absolute;
  bottom: -6px;
  left: 0;
  right: 0;
  font-size: 14px;
  width: 100px;
  margin: 0 auto;
  line-height: 1;
  text-align: center;
}

h2.section_title span {
  display: block;
  width: 300px;
  margin: 0 auto;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

h2.section_title span:before,
h2.section_title span:after {
  content: "";
  position: absolute;
  width: 60px;
  height: 2px;
  border-bottom: 2px solid;
  bottom: 0;
}

h2.section_title span:before {
  left: 50px;
}

h2.section_title span:after {
  right: 50px;
}

.section_head p {
  width: 66%;
  margin: 0 auto;
}

.sec-overlay h2.section_title,
.sec-overlay p,
.sec-overlay {
  color: #fff;
}

.one_half .section_head {
  text-align: left;
}

.sec-overlay {
  position: relative;
}

.sec-overlay:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(36, 44, 58, 0.8);
  position: absolute;
  top: 0;
  left: 0;
}

.custom-heading {
  margin-bottom: 30px;
}

.custom-heading h2 {
  font-family: "Roboto Condensed";
  font-size: 44px;
  text-transform: uppercase;
  font-weight: 700;
  position: relative;
  margin-bottom: 40px;
}

.custom-heading h2 span {
  display: block;
  font-size: 40%;
  margin-bottom: 10px;
  letter-spacing: 1px;
}

.animate-image img {
  -webkit-transition: opacity 1s, -webkit-transform 2s;
  transition: opacity 1s, transform 2s;
  -o-transition: opacity 2s, -o-transform 1s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.animate-image:hover img {
  opacity: 0.6;
  -webkit-transform: rotate(3deg) scale(1.2, 1.2);
  -o-transform: rotate(3deg) scale(1.2, 1.2);
  transform: rotate(3deg) scale(1.2, 1.2);
}

#image-slider .owl-pagination {
  position: absolute;
  bottom: 0;
  text-align: center;
  left: 0;
  right: 0;
}

#image-slider .owl-controls .owl-page {
  width: 15px;
  height: 15px;
  border-radius: 50px;
  background-color: #dda668;
  display: inline-block;
  margin: 5px 10px;
}

#image-slider .owl-controls .owl-page.active {
  background-color: #1a2c56;
}

.image-info-box {
  background-color: #242c3a;
  position: relative;
  text-align: center;
}

.image-info-thumb {
  overflow: hidden;
  position: relative;
}

.image-info-thumb img {
  -webkit-transition: opacity 1s, -webkit-transform 1s;
  transition: opacity 1s, transform 1s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.image-info-box:hover img {
  -webkit-transform: scale3d(1.1, 1.1, 1);
  transform: scale3d(1.1, 1.1, 1);
}

.image-info-cont {
  padding: 3em;
}

.image-info-cont h2 {
  color: #dda668;
}

.image-info-cont p {
  color: #fff;
}

.image-info-cont a {
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  display: block;
  margin-top: 15px;
}

.feature-box {
  position: relative;
  margin: 30px 0;
  overflow: hidden;
}

.feature-box-thumb img {
  width: -webkit-calc(100% + 50px);
  width: calc(100% + 50px);
  opacity: 1;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: translate3d(-40px, 0, 0);
  transform: translate3d(-40px, 0, 0);
  position: relative;
  display: block;
  max-width: none;
}

.featture-box-cont {
  position: absolute;
  bottom: 0;
  padding: 20px;
  background-color: rgba(36, 44, 58, 0.8);
  -webkit-transform: translate3d(0, 74%, 0);
  transform: translate3d(0, 74%, 0);
  -webkit-transition: opacity 0.2s, -webkit-transform 0.35s;
  transition: opacity 0.2s, transform 0.35s;
  color: #fff;
  width: 100%;
}

.featture-box-cont h2 {
  color: #dda668;
}

.featture-box-cont a {
  text-transform: uppercase;
  font-weight: 700;
}

.feature-box:hover .feature-box-thumb img {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.feature-box:hover .featture-box-cont {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.travern_check_room {
