.hotel-room {
  position: relative;
  margin-bottom: 20px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.4);
}

.hotel-room-thumb {
  position: relative;
  overflow: hidden;
}

.hotel-room-thumb img {
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.hotel-room:hover .hotel-room-thumb img {
  -webkit-transform: scale(1.15);
  transform: scale(1.15);
}

.room-thumb-overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: center;
  background-color: rgba(59, 44, 0.09, 0.6);
  text-transform: uppercase;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.6s;
  transition: opacity 0.35s, transform 0.6s;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.hotel-room:hover .room-thumb-overlay {
  -webkit-transform: scale(1);
  transform: scale(1);
}

.room-thumb-overlay a {
  background-color: #dda668;
  color: #fff;
  padding: 12px 30px;
  font-weight: 700;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.room-thumb-overlay a:hover {
  background-color: #fff;
  color: #dda668;
}

.hotel-room-cont {
  background-color: #fff;
  padding: 20px;
}

.hotel-room-cont h3 {
  margin-bottom: 10px;
}

.hotel-room-price {
  margin-top: 10px;
  font-size: 26px;
  color: #242c3a;
}

.hotel-room-price span {
  display: inline-block;
  margin-left: 10px;
  font-size: 16px;
}

.icon-info-box {
  text-align: center;
}

.icon-top {
  margin: 15px auto 30px;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  text-align: center;
  position: relative;
  z-index: 1;
  color: #fff;
  border: 3px solid #dda668;
  -webkit-transition: color 0.3s;
  -moz-transition: color 0.3s;
  transition: color 0.3s;
  font-size: 34px;
  line-height: 84px;
}

.icon-top:after {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2px;
  z-index: -1;
  background-color: #dda668;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  -webkit-transition: -webkit-transform 0.2s, opacity 0.2s;
  -moz-transition: -moz-transform 0.2s, opacity 0.2s;
  transition: transform 0.2s, opacity 0.2s;
  content: "";
}

.icon-info-box:hover .icon-top::after {
  -webkit-transform: scale(0.85);
  -moz-transform: scale(0.85);
  -ms-transform: scale(0.85);
  transform: scale(0.85);
}

.image_info {
